import React from 'react';
import avatar2 from '../images/avatar2.jpg';
import adcom from '../images/crop-adcom.jpg';
import api from '../images/crop-api.jpg';
import dashboard from '../images/crop-dashboard.jpg';
import game from '../images/crop-game.jpg';
import video from '../images/crop-video-player.jpg';
import reactGallery from '../images/crop-react-gallery.jpg';

class BigSlider extends React.Component {

  // Default target slide (to be used for BigSlider) to 0 (Personal Bio)
  constructor() {
    super();
    this.state = {
      targetSlide: 0
    };
  }

  // When screen width is less than 768, switch slide to 0 (Personal Bio)
  // Avoid repetition, as all the portfolio items display in a column on small screens
  updateWidth(){
    if (window.innerWidth < 768 && this.state.targetSlide !== 0){
      this.setState({
          targetSlide: 0      
        }, function(){    
        console.log(`new slide is ${this.state.targetSlide}`)},

      );
    }
  }

  // Adds listener for checking if screen is small enough to swith to slide 0 for mobile mode
  componentDidMount() {
    window.addEventListener("resize", this.updateWidth.bind(this));
  }

  // When top level of app passes a new target slide, update state, and render new slide
  componentDidUpdate(prevProps) {  
  if (this.props.targetSlide !== prevProps.targetSlide){
    this.setState({
      targetSlide: this.props.targetSlide      
    }, function(){    
    console.log(`new slide is ${this.state.targetSlide}`)},

  );

  }
  }

  // Render target slide
  render() {
    if (this.state.targetSlide == 0) {    
    return (
      <div className="container">

          <div className="intro">
            <img id="main-avatar" src={avatar2} alt="avatar" width={239} height={332}/>
            <p>
              Hi! I'm a <strong>front-end developer</strong>.
            </p>
            <p>
              My main technologies are Wordpress, React, Javascript, CSS, and HTML.              
            </p>
            <p>
              My passion is for health and learning.
            </p>
            <div className="homeslide-nav">
              <p onClick={() => this.setState({targetSlide: 6    })} >
                Back
              </p>
              <p onClick={() => this.setState({targetSlide: 1    })} >
                Forward
              </p>
            </div>
          </div>
      </div>
    );
  }

  else if (this.state.targetSlide == 1) {    
    return (
      <div className="container">

          <div className="intro">
            <img className="portfolio-image" src={dashboard} alt="portfolio-image" width={509} height={332} />
            <h1>Web App Dashboard</h1>
            <p className="portfolio-item-description-big">
            A dashboard with javascript for basic notification and settings functionality, as well as several Chart.js graphs and an auto complete for the user search
            </p>
            <p className="portfolio-item-links-big">
              <a href="https://akassabian.github.io/web-app-dashboard" target="_blank" rel="noopener noreferrer">Live</a>
              <a href="https://github.com/akassabian/web-app-dashboard" target="_blank" rel="noopener noreferrer">Repo</a>
            </p>
            <div className="slide-nav">
              <p onClick={() => this.setState({targetSlide: 6    })} >
                Back
              </p>
              <p className="return-home" onClick={() => this.setState({targetSlide: 0    })} >
                Return home
              </p>
              <p onClick={() => this.setState({targetSlide: this.state.targetSlide + 1    })} >
                Forward
              </p>
            </div>
          </div>
      </div>
    );
  }
  
  else if (this.state.targetSlide == 2) {    
    return (
      <div className="container">

          <div className="intro">
            <img className="portfolio-image" src={adcom} alt="portfolio-image" width={509} height={332} />
            <h1>Personal Website</h1>
            <p className="portfolio-item-description-big">
            A personal website with a custom WordPress theme based on Bootstrap and Sass compiled by Grunt
            </p>
            <p className="portfolio-item-links-big">
              <a href="http://alainkassabian.com" target="_blank" rel="noopener noreferrer">Live</a>
              <a href="https://github.com/akassabian/alainkassabian.com-source-wordpress" target="_blank" rel="noopener noreferrer">Repo</a>
            </p>
            <div className="slide-nav">
              <p onClick={() => this.setState({targetSlide: this.state.targetSlide - 1    })} >
                Back
              </p>
              <p className="return-home" onClick={() => this.setState({targetSlide: 0    })} >
                Return home
              </p>
              <p onClick={() => this.setState({targetSlide: this.state.targetSlide + 1    })} >
                Forward
              </p>
            </div>
          </div>
      </div>
    );
  }

  else if (this.state.targetSlide == 3) {    
    return (
      <div className="container">

          <div className="intro">
            <img className="portfolio-image" src={api} alt="portfolio-image" width={509} height={332} />
            <h1>API Employee Directory</h1>
            <p className="portfolio-item-description-big">
            Searchable directory populated by Jquery call to Randomuser.me API - pure css overlay modals with navigation for expanded detail
            </p>
            <p className="portfolio-item-links-big">
              <a href="https://akassabian.github.io/API-Employee-Directory" target="_blank" rel="noopener noreferrer">Live</a>
              <a href="https://github.com/akassabian/API-Employee-Directory" target="_blank" rel="noopener noreferrer">Repo</a>
            </p>
            <div className="slide-nav">
              <p onClick={() => this.setState({targetSlide: this.state.targetSlide - 1    })} >
                Back
              </p>
              <p className="return-home" onClick={() => this.setState({targetSlide: 0    })} >
                Return home
              </p>
              <p onClick={() => this.setState({targetSlide: this.state.targetSlide + 1    })} >
                Forward
              </p>
            </div>
          </div>
      </div>
    );
  }

  else if (this.state.targetSlide == 4) {    
    return (
      <div className="container">

          <div className="intro">
            <img className="portfolio-image" src={game} alt="portfolio-image" width={509} height={332} />
            <h1>Game Show App</h1>
            <p className="portfolio-item-description-big">
            Game Show App populated with randomized phrases and based on Javascript DOM manipulation
            </p>
            <p className="portfolio-item-links-big">
              <a href="https://akassabian.github.io/Game-Show-App" target="_blank" rel="noopener noreferrer">Live</a>
              <a href="https://github.com/akassabian/Game-Show-App" target="_blank" rel="noopener noreferrer">Repo</a>
            </p>
            <div className="slide-nav">
              <p onClick={() => this.setState({targetSlide: this.state.targetSlide - 1    })} >
                Back
              </p>
              <p className="return-home" onClick={() => this.setState({targetSlide: 0    })} >
                Return home
              </p>
              <p onClick={() => this.setState({targetSlide: this.state.targetSlide + 1    })} >
                Forward
              </p>
            </div>
          </div>
      </div>
    );
  }

  else if (this.state.targetSlide == 5) {    
    return (
      <div className="container">

          <div className="intro">
            <img className="portfolio-image" src={video} alt="portfolio-image" width={509} height={332} />
            <h1>Interactive Video Player</h1>
            <p className="portfolio-item-description-big">
            A video player built from MediaElement.js with custom javascript for itneractive transcript that highlights as video plays and moves the video to matching time of any text that is clicked
            </p>
            <p className="portfolio-item-links-big">
              <a href="https://akassabian.github.io/Interactive-Video-Player" target="_blank" rel="noopener noreferrer">Live</a>
              <a href="https://github.com/akassabian/Interactive-Video-Player" target="_blank" rel="noopener noreferrer">Repo</a>
            </p>
            <div className="slide-nav">
              <p onClick={() => this.setState({targetSlide: this.state.targetSlide - 1    })} >
                Back
              </p>
              <p className="return-home" onClick={() => this.setState({targetSlide: 0    })} >
                Return home
              </p>
              <p onClick={() => this.setState({targetSlide: this.state.targetSlide + 1    })} >
                Forward
              </p>
            </div>
          </div>
      </div>
    );
  }

  else if (this.state.targetSlide == 6) {    
    return (
      <div className="container">

          <div className="intro">
            <img className="portfolio-image" src={reactGallery} alt="portfolio-image" width={509} height={332} />
            <h1>React Photo Gallery</h1>
            <p className="portfolio-item-description-big">
            Photo gallery written in React - searchable, with Axios retrieving images from Flickr API
            </p>
            <p className="portfolio-item-links-big">
              <text><span>No live link as <br /> Flickr's API is private</span></text>
              <a href="https://github.com/akassabian/techdegree-project-11" target="_blank" rel="noopener noreferrer">Repo</a>
            </p> 
            <div className="slide-nav">
              <p onClick={() => this.setState({targetSlide: this.state.targetSlide - 1    })} >
                Back
              </p>
              <p className="return-home" onClick={() => this.setState({targetSlide: 0    })} >
                Return home
              </p>
              <p onClick={() => this.setState({targetSlide: 1   })} >
                Forward
              </p>
            </div>
          </div>
      </div>
    );
  }


  }
}

export default BigSlider;


