import React, { Component } from 'react';

class Skills extends Component {

    render() {
        return (
            <section id="skills-section">
                <div id="skills">
                    <h3><strong>SKILLS</strong></h3>
                    <ul>
                        <li>Creating responsive media with HTML5, CSS, JSON, PHP, SASS, SQL, and Bootstrap</li>
                        <li>Adding interactivity with Javascript and Jquery</li>
                        <li>DOM manipulation with React</li>
                        <li>Creating and maintaining WordPress websites (custom or prebuilt themes)</li>
                        <li>Coding emails efficiently with Gulp and Zurb's Foundation for Emails</li>
                        <li>Managing mailing lists with tools like MailChimp</li>
                        <li>Editing and creating MySQL databases</li>
                        <li>Activating and mantaining HTTPS</li>
                        <li>Using tools like AXIOS and AJAX to integrate APIs such as Flickr and Twitter</li>
                        <li>Managing Linux servers and hosting with CentOS, SSH, WHM/cPanel, Virtualmin and Sentora</li>
                        <li>Maintaining backups with Bitbucket and Git</li>
                        <li>SEO with tools like Yoast, XML, GTmetrix, and Google Analytics</li>
                        <li>Streamlining workflow with Grunt and Sublime</li>
                        <li>Styling graphics and colours with software like Photoshop and Adobe Premiere</li>
                        <li>Reviewing peer projects as a Techdegree Student Developer Moderator on Treehouse Slack</li>
                        <li>Maintaining organized research files for projects of varying relevance and urgency</li>
                        <li>"Getting Things Done” approach to managing time to keep several projects moving forward</li>
                    </ul>
                </div>
            </section>
        )
    }
}

export default Skills;


