import React, { Component } from 'react';
import './App.css';
import Scrollspy from 'react-scrollspy';
import BigSlider from './components/BigSlider.js'
import Contact from './components/Contact.js'
import PortfolioMin from './components/PortfolioMin.js'
import Skills from './components/Skills.js'
import SmallSlider from './components/SmallSlider.js'

class App extends Component {

  // Default target slide (to be used for BigSlider) to 0 (Personal Bio)
  constructor() {
    super();
    this.state = {
      targetSlide: 0
    };
  }

  // Updates state based on which slide on the SmallSlider is clicked (loads larger image and discription)
  getData = targetSlide => {
    console.log(`Target slide at top of app: ${targetSlide}`);
    this.setState({ targetSlide: targetSlide }, function(){
      console.log(`target slide state at to of app: ${this.state.targetSlide}`);

    });      
  }

  render() {    
    
    return ( 
      
        <div>
          <link rel="stylesheet" type="text/css" charSet="UTF-8" href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick.min.css" />
          <link rel="stylesheet" type="text/css" href="https://cdnjs.cloudflare.com/ajax/libs/slick-carousel/1.6.0/slick-theme.min.css" />
          <div className="header-outer">
          <div className="header">
            <div className="logo">
              <h1>Alain Kassabian</h1>
            </div>
            <div className="main-nav">
              <nav>
                <Scrollspy items={ ['portfolio-section', 'skills-section', 'contact-section']} currentClassName="is-current" className="nav">                  
                  <li><a href="#portfolio-section"><h2>Portfolio</h2></a></li>
                  <li><a href="#skills-section"><h2>Skills</h2></a></li>
                  <li><a href="#contact-section"><h2>Contact</h2></a></li>
                </Scrollspy>
              </nav>
            </div>
            </div>
          </div>

          <div className="main-content">

            <BigSlider targetSlide = {this.state.targetSlide} />
            
            <section id="portfolio-section">
            <div id="portfolio">
              <h3><strong>PORTFOLIO</strong></h3>
              <div id="portfolio-768">
              <SmallSlider sendData={this.getData} />   
              </div>

              <PortfolioMin />
            </div>
            </section>            

            <Skills />

            <Contact />

          </div>            

          <footer>
            <p className="footername"><strong>Alain Kassabian</strong></p>            
            <nav className="bottom_nav">
              <ul>
                <li>C 2020. All Rights Reserved.</li>
              </ul>
            </nav>
          </footer>
        </div>
      

    );
  }
}

export default App;



