import React, { Component } from 'react';
import email from '../images/email.svg';
import linkedin from '../images/linkedin.svg';
import twitter from '../images/twitter.svg';

class Contact extends Component {

    render() {
        return (
            <section id="contact-section">
                <div id="contact">
                    <h3><strong>CONTACT</strong></h3>
                    <div id="contact--icons">            
                        <a href="https://twitter.com/alainkassabian"><img className="twitter"src={twitter} alt="twitter" width={75} height={75} /></a>
                        <a href="https://www.linkedin.com/in/alain-kassabian"><img className="linkedin" src={linkedin} alt="linkedin" width={50} height={50} /></a>
                        <a href="mailto:alainkassabian@gmail.com"><img className="email" src={email} alt="email" width={50} height={50} /></a>
                    </div>
                    <p>Reach out, if you'd like to chat!</p>
                </div>
            </section>
        )
    }
}

export default Contact;


