import React from 'react';
import Slider from "react-slick";
import adcom from '../images/crop-adcom.jpg';
import api from '../images/crop-api.jpg';
import dashboard from '../images/crop-dashboard.jpg';
import game from '../images/crop-game.jpg';
import video from '../images/crop-video-player.jpg';
import reactGallery from '../images/crop-react-gallery.jpg';

class SmallSlider extends React.Component {
  render() {
    var settings = {

      autoplay: true,
      autoplaySpeed: 0,
      centerMode: true,
      slidesToShow: 3,
      speed: 20000,
      cssEase: 'linear',
      arrows: false,
      pauseOnHover: false
    };
    return (
      <div className="container">
        <Slider {...settings}>
          <div>
            <img src={dashboard} alt="Screenshot of Dashboard App" width={401} height={261} onClick={() => this.props.sendData('1')} />
          </div>
          <div>
            <img src={adcom} alt="Screenshot of Personal Website" width={401} height={261} onClick={() => this.props.sendData('2')} />
          </div>
          <div>
            <img src={api} alt="Screenshot of API App" width={401} height={261} onClick={() => this.props.sendData('3')} />
          </div>
          <div>
            <img src={game} alt="Screenshot of Gameshow App" width={401} height={261} onClick={() => this.props.sendData('4')} />
          </div>
          <div>
            <img src={video} alt="Screenshot of Interactive Video Player" width={401} height={261} onClick={() => this.props.sendData('5')} />
          </div>
          <div>
            <img src={reactGallery} alt="Screenshot of React Gallery" width={401} height={261} onClick={() => this.props.sendData('6')} />
          </div>
        </Slider>
      </div>
    );
  }
}

export default SmallSlider;


